// Import word selection functions
import { 
  getWordFrequency, 
  getWordDifficulty, 
  selectWords, 
  selectDailyWord, 
  selectCrosswordWords,
  type WordEntry,
  type WordSelectionParams,
  type WordSelectionResult 
} from './wordSelector';

import { HAWAIIAN_CONFIG } from './frequency-types';

// Re-export for backward compatibility
export { 
  getWordFrequency, 
  getWordDifficulty, 
  selectWords, 
  selectDailyWord, 
  selectCrosswordWords,
  type WordSelectionParams,
  type WordSelectionResult 
};

// Legacy compatibility functions
export const getWordsByLength = (length: number): WordEntry[] =>
  HAWAIIAN_WORDS.filter(entry => entry.word.length === length);

export const getWordsByDifficulty = (difficulty: 'beginner' | 'intermediate' | 'advanced'): WordEntry[] =>
  HAWAIIAN_WORDS.filter(entry => getWordDifficulty(entry, CONFIG) === difficulty);

export const getWordsByFrequency = (minFrequency: number): WordEntry[] =>
  HAWAIIAN_WORDS.filter(entry => getWordFrequency(entry, CONFIG) >= minFrequency);

// Game integration functions
export const getKimiKupuDailyWord = () => 
  selectDailyWord(HAWAIIAN_WORDS, CONFIG);

export const getPangaKupuWordPool = (targetCount: number = 30) => 
  selectCrosswordWords(HAWAIIAN_WORDS, CONFIG, [3, 4, 5, 6, 7, 8], targetCount);

